* > a {
  color: inherit;
}

.mn-sg-sidebar_component {
  width: 100%;
  max-width: 20rem;
  background-color: #fff;
  position: fixed;
  top: 68px;
  bottom: 0;
  left: 0;
  right: auto;
}

.mn-sg-sidebar_wrap {
  border-right: 1px solid rgba(0, 0, 0, 0.07);
  position: absolute;
  top: 0%;
  bottom: 0%;
  left: 0%;
  right: 0%;
  overflow: auto;
}

.mn-sg-sidebar_menu-item {
  flex-direction: column;
  flex: 1;
  display: flex;
  position: relative;
  cursor: pointer;
}

.mn-sg-sidebar_menu-item-link {
  width: 100%;
  padding: 0.5rem 1.75rem;
  font-family: Ballinger, sans-serif;
  font-size: 1.125rem;
  font-weight: 400;
  display: inline-block;
}

.page-code_wrap {
  display: none;
}

.mn-sg-sidebar_menu-item-sublinks {
  height: 0;
  overflow: hidden;
}

.mn-sg-sidebar_menu-item-sublink {
  width: 100%;
  padding: 0.5rem 1.5rem 0.5rem 3rem;
  font-family: Ballinger, sans-serif;
  font-size: 1rem;
  display: inline-block;
}

.page-code_style-guide {
  display: none;
}

.mn-sg-content_component {
  display: flex;
}

.mn-sg-header_component {
  z-index: 999;
  width: 100%;
  background-color: #fff;
  position: fixed;
  top: 0;
  bottom: auto;
  left: 0;
  right: 0;
}

.mn-sg_fake-sidebar {
  width: 20rem;
}

.mn-sg_main {
  flex: 1;
}

.mn-sg_category-wrap {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 4.25rem;
  padding-bottom: 4.25rem;
  display: flex;
  position: relative;
  overflow: hidden;
}

.mn-sg_category-inner {
  width: 100%;
  max-width: 64rem;
  padding-left: 3rem;
  padding-right: 3rem;
}

.mn-sg_category-item-descr-rt {
  opacity: 0.6;
  font-family: Ballinger, sans-serif;
  font-weight: 400;
}

.mn-sg_category-item-descr-rt h1 {
  margin-top: 3rem;
  margin-bottom: 1.5rem;
  font-size: 3rem;
  font-weight: 500;
  line-height: 1.3;
}

.mn-sg_category-item-descr-rt h2 {
  margin-top: 1.5rem;
  margin-bottom: 0.75rem;
  font-size: 2rem;
  font-weight: 500;
  line-height: 1.3;
}

.mn-sg_category-item-descr-rt h4 {
  margin-top: 1.5rem;
  margin-bottom: 0.75rem;
  font-size: 1.25rem;
  font-weight: 700;
}

.mn-sg_category-item-descr-rt h5 {
  margin-top: 1rem;
  margin-bottom: 0.325rem;
  font-size: 1.125rem;
  font-weight: 500;
  line-height: 1.5;
}

.mn-sg_category-item-descr-rt h6 {
  margin-top: 1rem;
  margin-bottom: 0.325rem;
  font-size: 1rem;
  line-height: 1.5;
}

.mn-sg_category-item-descr-rt p {
  font-size: 0.875rem;
  line-height: 1.6;
}

.mn-sg_category-item-descr-rt h3 {
  margin-top: 1.5rem;
  margin-bottom: 0.75rem;
  font-size: 1.5rem;
  font-weight: 500;
  line-height: 1.3;
}

.mn-sg_header-spacer {
  padding-top: 4.25rem;
}

.mn-sg-header_inner {
  height: 4.25rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.07);
  justify-content: space-between;
  align-items: center;
  padding-left: 1.75rem;
  padding-right: 1.75rem;
  display: flex;
}

.mn-sg_header-logo-wrap {
  font-family: Ballinger, sans-serif;
  font-size: 1.5rem;
  font-color: rgba(0, 0, 0) !important;
}

.text-block {
  letter-spacing: -0.5px;
  font-weight: 500;
}

.mn-sg_header-logo-span {
  color: rgba(0, 0, 0, 0.4);
  margin-left: 0.125rem;
  font-weight: 400;
}

.mn-sg-sidebar_toggle-btn-wrap {
  z-index: 999;
  justify-content: center;
  align-items: center;
  display: none;
}

.mn-sg-sidebar_btn-icon-open {
  width: 1.5rem;
  height: 1.5rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.mn-sg-sidebar_toggle-btn {
  width: 3.5rem;
  height: 3.5rem;
  cursor: pointer;
  background-color: #ececec;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.mn-sg-sidebar_menu-item-toggle-btn-icon {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0%;
  bottom: 0%;
  left: 0%;
  right: 0%;
}

.mn-sg-sidebar_btn-icon-close {
  width: 1.5rem;
  height: 1.5rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.mn-sg-sidebar_menu-item-toggle-btn {
  width: 1.5rem;
  height: 1.5rem;
  position: absolute;
  top: auto;
  bottom: auto;
  left: auto;
  right: 0;
  transform: translate(-1.5rem, -0.25rem) translate(0, 50%);
}

.mn-sg_typography-list {
  grid-row-gap: 6rem;
  grid-template-rows: auto auto;
  grid-template-columns: 1fr;
  grid-auto-columns: 1fr;
  display: grid;
}

.mn-sg_typography-item {
  grid-row-gap: 1.5rem;
  grid-template-rows: auto auto;
  grid-template-columns: 1fr;
  grid-auto-columns: 1fr;
  display: grid;
}

.mn-sg_category-item-top-list {
  grid-column-gap: 3rem;
  grid-row-gap: 1rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.07);
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-end;
  padding-bottom: 1rem;
  display: flex;
}

.mn-sg_variable {
  grid-column-gap: 0.25rem;
  grid-row-gap: 0.25rem;
  color: rgba(255, 255, 255, 0.8);
  background-color: #1e1e1e;
  border-radius: 0.25rem;
  align-items: center;
  padding: 0.25rem 0.5rem;
  font-size: 0.75rem;
  display: flex;
}

.mn-sg_tag-icon {
  width: 1.25rem;
  height: 1.25rem;
  color: rgba(255, 255, 255, 0.9);
  border-radius: 0.5rem;
  display: inline-block;
}

.mn-sg_tag-text {
  font-family: Ballinger, sans-serif;
  line-height: 1;
  display: inline-block;
}

.mn-sg_tag {
  min-height: 1.75rem;
  grid-column-gap: 0.25rem;
  grid-row-gap: 0.25rem;
  color: #fff;
  white-space: nowrap;
  border-radius: 0.25rem;
  align-items: center;
  padding: 0.25rem 0.5rem;
  font-size: 0.75rem;
  display: flex;
}

.mn-sg_tag.is-class {
  background-color: #006acc;
}

.mn-sg_tag.is-html {
  background-color: #be4aa5;
}

.mn-sg_tag.is-variable {
  background-color: #1e1e1e;
}

.mn-sg_category-item-details-wrap {
  grid-column-gap: 1.5rem;
  grid-row-gap: 0.5rem;
  flex-wrap: wrap;
  align-items: center;
  display: flex;
}

.mn-sg_category-item-title-text {
  opacity: 1;
  font-family: Ballinger, sans-serif;
  font-size: 1rem;
  font-weight: 500;
}

.mn-sg_category-item-descr-text {
  color: rgba(0, 0, 0, 0.3);
  font-family: Ballinger, sans-serif;
  font-size: 0.875rem;
  font-weight: 400;
}

.mn-sg_tags-wrap {
  grid-column-gap: 0.5rem;
  grid-row-gap: 0.5rem;
  flex-direction: row;
  display: flex;
}

.mn-sg-embed_component {
  width: 100%;
  min-height: 16rem;
  background-color: #2e2e2e;
  border-radius: 0.25rem;
  grid-template-rows: minmax(auto, max-content) auto;
  grid-template-columns: 1fr;
  grid-auto-columns: 1fr;
  display: grid;
}

.mn-sg-embed_body-wrap {
  width: 100%;
  padding: 1rem;
}

.mn-sg-embed_header {
  border-bottom: 1px solid rgba(255, 255, 255, 0.13);
  align-items: center;
  display: flex;
}

.mn-sg-embed_header-text {
  color: #fff;
  flex: 1;
  padding-left: 0.5rem;
  font-size: 0.875rem;
}

.mn-sg-embed_header-icon-wrap {
  width: 2rem;
  height: 2rem;
  color: #fff;
  justify-content: center;
  align-items: center;
  display: flex;
}

.mn-sg-embed_header-icon-wrap.is-close {
  border-left: 1px solid rgba(255, 255, 255, 0.13);
}

.mn-sg-embed_header-icon {
  width: 1rem;
  height: 1rem;
  display: flex;
}

.mn-sg-embed_body-title-text {
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 0.5rem;
  font-size: 0.75rem;
}

.mn-sg-embed_token-input {
  min-height: 10rem;
  background-color: rgba(0, 0, 0, 0.15);
  border: 1px solid rgba(255, 255, 255, 0.14);
  border-radius: 0.25rem;
  position: relative;
}

.mn-sg-embed_rt-wrap {
  margin-top: 0.25rem;
  margin-left: 1.5rem;
  padding-left: 0.25rem;
}

.mn-sg-embed_rt {
  color: #fff;
}

.mn-sg-embed_rt h2 {
  color: #80cbc4;
  font-size: 0.75rem;
  display: inline;
}

.mn-sg-embed_rt p,
.mn-sg-embed_rt h4 {
  font-size: 0.75rem;
}

.mn-sg-embed_rt h1 {
  color: #546e7a;
  font-size: 0.75rem;
}

.mn-sg-embed_default-text {
  color: #fff;
  font-size: 0.75rem;
  line-height: 1.3;
}

.mn-sg-embed_default-text.is-index {
  color: #537f7e;
}

.mn-sg-embed_default-text.is-tag {
  color: #80cbc4;
}

.mn-sg-embed_default-text.is-comment {
  color: #546e7a;
}

.mn-sg-embed_default-text.is-prop {
  color: #80cbae;
}

.mn-sg-embed_default-text.is-string {
  color: #c3e88d;
}

.mn-sg-embed_default-text.is-atom {
  color: #f77669;
}

.mn-sg-embed_index {
  width: 1.5rem;
  background-color: rgba(0, 0, 0, 0.13);
  border-right: 1px solid rgba(255, 255, 255, 0.13);
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-top: 0.25rem;
  display: flex;
  position: absolute;
  top: 0%;
  bottom: 0%;
  left: 0%;
  right: auto;
}

.mn-sg-embed_default-text-prop {
  color: #80cbae;
}

.mn-sg_category-header-wrap {
  margin-bottom: 4rem;
}

.mn-sg_grid-template-item {
  background-color: #fff0f0;
  border: 1px dashed rgba(255, 0, 0, 0.3);
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
}

.mn-sg_container-template {
  border: 1px dashed rgba(255, 0, 0, 0.3);
}

.mn-sg_container-template-inner {
  background-color: #fff1f1;
  border: 1px rgba(255, 0, 0, 0.6);
  border-left-color: rgba(255, 0, 0, 0.3);
  border-right: 1px dashed rgba(255, 0, 0, 0.3);
  padding: 1.5rem;
}

.mn-sg_category-item {
  margin-bottom: 4rem;
}

.mn-sg_category-item-top-wrap {
  margin-bottom: 3rem;
}

.mn-sg_category-header-rt {
  opacity: 0.8;
  margin-top: 0.5rem;
}

.test {
  background-color: #ffc6c6;
}

@media screen and (max-width: 991px) {
  .mn-sg-sidebar_component {
    z-index: 998;
    transform: translate(-100%);
  }

  .mn-sg-sidebar_menu-item-link {
    padding-left: 3rem;
    padding-right: 3rem;
  }

  .mn-sg_fake-sidebar {
    display: none;
  }

  .mn-sg_category-wrap {
    padding-top: 3rem;
    padding-bottom: 3rem;
  }

  .mn-sg_category-item-descr-rt {
    z-index: 1;
  }

  .mn-sg-header_inner {
    padding-left: 3rem;
    padding-right: 3rem;
  }

  .mn-sg-sidebar_toggle-btn-wrap {
    z-index: 999;
    display: flex;
    position: fixed;
    top: auto;
    bottom: 3rem;
    left: 0;
    right: 0;
  }

  .mn-sg-sidebar_btn-icon-open {
    color: #000;
    position: absolute;
  }

  .mn-sg-sidebar_toggle-btn {
    border: 1px solid rgba(0, 0, 0, 0.07);
  }

  .mn-sg-sidebar_btn-icon-close {
    width: 1.25rem;
    height: 1.25rem;
    opacity: 0;
    position: absolute;
  }
}

@media screen and (max-width: 767px) {
  .mn-sg-sidebar_menu-item-link {
    padding-left: 1.75rem;
    padding-right: 1.75rem;
  }

  .mn-sg_category-wrap {
    padding-top: 1.75rem;
    padding-bottom: 1.75rem;
  }

  .mn-sg_category-inner {
    padding-left: 1.75rem;
    padding-right: 1.75rem;
  }

  .mn-sg_category-item-descr-rt h1 {
    margin-bottom: 0.75rem;
    font-size: 2.25rem;
  }

  .mn-sg_category-item-descr-rt h2 {
    font-size: 1.5rem;
  }

  .mn-sg_category-item-descr-rt h4 {
    font-size: 1.125rem;
  }

  .mn-sg_category-item-descr-rt h5 {
    font-size: 1rem;
  }

  .mn-sg_category-item-descr-rt h3 {
    font-size: 1.25rem;
  }

  .mn-sg-header_inner {
    padding-left: 1.75rem;
    padding-right: 1.75rem;
  }
}

@media screen and (max-width: 479px) {
  .mn-sg-sidebar_component {
    max-width: none;
  }
}

.mn-sg-sidebar_menu-item:first-child {
  padding-top: 0.75rem;
}

.mn-sg-sidebar_menu-item:last-child {
  padding-bottom: 0.75rem;
}

.mn-sg-sidebar_menu-item::after {
  margin-top: 0.75rem;
  margin-bottom: 0.75rem;
  content: '';
  width: 100%;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.07);
}

.mn-sg-sidebar_menu-item:last-child::after {
  content: none;
}

.mn-sg-sidebar_toggle-btn-wrap {
  pointer-events: none;
}

.mn-sg-sidebar_toggle-btn {
  pointer-events: auto;
}
